.footer {
    position: fixed;
    left: 0;
    bottom: -150px;
    /* bottom: 0; */
    width: 100%;
    background-image: -webkit-gradient(linear, left top, right bottom, from(#636363), color-stop(#525252), color-stop(#424242), color-stop(#323232), to(#232323));
    background-image: -webkit-linear-gradient(left top, #636363, #525252, #424242, #323232, #232323);
    background-image: -o-linear-gradient(left top, #636363, #525252, #424242, #323232, #232323);
    background-image: linear-gradient(to right bottom, #636363, #525252, #424242, #323232, #232323);
    text-align: center;
    height: 200px;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: end;
        align-items: flex-end;
    -ms-flex-direction: column;
        flex-direction: column;
-webkit-box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.4);
        box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.4);
}
.footer:hover {
    bottom: 0;
}

.zonaPatrocinadores{
    height: 90%;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: end;
        align-items: flex-end;
}

.patro1{
    height: 100%;
    width: 33%;
    background: url('../assets/img/logo\ aula\ universitaria.png') no-repeat center;
    background-size: 33%;
}
