    color: #fff;
}

.agradecimientos{
    width: 100%;
    /* background-color: #fff; */
    font-size: 1.3em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    letter-spacing: 10px;
}

.fotterZonaText{
    width: 100%;
