}

.paux{
    width: 33%;
}

.contacto{
    width: 33%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-pack: center;
        justify-content: center;
    padding-left: 10px;
}


