.topBar{
    width: 100%;
    height: 100px;
    position: fixed;
    top: 0px;
    /* overflow: hidden; */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    /* box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19),
      0 6px 6px rgba(0, 0, 0, 0.23); */
    z-index: 20;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}
.topBarN{
    width: 100%;
    height: 100px;
    background-image: -webkit-gradient(linear, left top, right bottom, from(#636363), color-stop(#525252), color-stop(#424242), color-stop(#323232), to(#232323));
    background-image: -webkit-linear-gradient(left top, #636363, #525252, #424242, #323232, #232323);
    background-image: -o-linear-gradient(left top, #636363, #525252, #424242, #323232, #232323);
    background-image: linear-gradient(to right bottom, #636363, #525252, #424242, #323232, #232323);
    position: fixed;
    top: 0px;
    /* overflow: hidden; */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19),
      0 6px 6px rgba(0, 0, 0, 0.23);
            box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19),
      0 6px 6px rgba(0, 0, 0, 0.23);
    z-index: 20;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

